@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');


/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  box-sizing: border-box;


}


.container {  
  min-width: 280px;
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
  height: 100vh;
}


.navbar {
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 500px;
  margin: 0 auto;
  border-bottom: 3px solid #f2f2f2;
  
}


.navbar h1 {
  color: #f1356d;
  font-size: 4vh;
}

.navbar h2 {
  color: black;
  font-size: 3vh;
  margin-top: 1vh;
}



/* home styles */

.home-container {
  position: relative;
}


.home-container-create-game {
  margin: 0 auto;
  max-width: 500px;
}


.home-container h3{
	margin-bottom: 10px;
  padding-left: 20px;
  font-size: 2.5vh;
}

.home-container p {
  margin-top: 20px;
	margin-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 2vh;
}



.add-player-container {
  display: flex;
  margin: 0 auto;
  cursor: pointer;
  justify-content: center;

}


.home-button-container {
  display: flex;
  justify-content: center;
  align-items: center; 
}




.home-button-container button {
  display: inline-block;
  background: #f1356d;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2vh;
  font-family: inherit;
  margin-top: 30px;
  font-weight: bold;
}


.home-button-container button:focus {
  outline: none;
}



.addPlayersContainer {
  max-width: 500px;
  margin: 0 auto;
  text-align:center;
}



.add-player-form {
  font-weight:bold;

}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: inline-block;
}

.form-control input {
  width: 80%;
  height: 5vh;
  margin: 5px;
  padding: 3px 7px;
  font-size: 2vh;
}

.form-control input:focus{
  outline: none;
}





/* Score Board */


.table-container table {
  height: 92vh;
  width: 100%;
}

.table-container th {
  border: 1px solid #ccc;
}

.table-container {
  overflow-x: scroll;
  margin-bottom: 10px;
  font-size: 2vh;
}


.table-container th:first-child {
  position: sticky;
  white-space: nowrap;
  text-align:left;
  width: 30%;
  background-color: #ccc;
  left: 0;
}


.table-container td {
  text-align:center;
  border: 1px solid #ccc;
  font-weight:bold;
  min-width: 125px;
}

tr:nth-child(even) {background-color: #f2f2f2;}



.playerNames {
  color:red;
}










.changeScore {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  max-width: 375px;
  z-index: 200;
  
}

.changeScore-header  {
  background-color:#4b0082;
  color:Gainsboro;
  text-align:left;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 0 0 1000px rgba(0,0,0,.3);
  
}

.changeScore-header p {
  color:Gainsboro;
  text-align:left;
  padding: 12px 0px 0px 12px;
  margin:0px;
  text-transform:capitalize;
}

.changeScore-header h3{
  color:white;
  text-align:left;
  padding: 0px 0px 12px 12px;
  margin:0px;
}


.changeScore-body {
  background-color:white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}



.changeScore-body-buttons-container {
  padding:20px;

}


.changeScore-body-buttons-container button {
  font-size:16px;
  font-weight: bold;
  width:40px;
  height:40px;
  border: 1px solid lightgrey;
  background-color:white;
  cursor:pointer;

}



.changeScore-body-buttons-container button:focus{
  background-color:#f1356d;
  color:white;
  outline: #f1356d;

}


.changeScore-body-buttons-container .deleteButtonScoreChange:focus {
  background-color:white;
  color:white;
  outline: #f1356d;

}


.cancelScoreChangeButton {
  border:0px;
  background-color: white;
  font-weight:bold;
  margin-right: 50px;
  margin-bottom: 16px;
  cursor:pointer;
}


.cancelScoreChangeButton:focus {
  outline: none;
}


.scoreChangeConfirm {
  color:white;
  font-weight:bold;
  background-color:#f1356d;
  border:0px;
  padding: 5px 10px;
  border-radius: 5px;
  margin-left: 50px;
  margin-bottom: 16px;
  cursor:pointer;
}


.scoreChangeConfirm:disabled {
  cursor:context-menu;
  background-color:#ffc6d7;
}


.scoreChangeConfirm:focus {
  outline: none;
}




.confirmComplete {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  max-width: 375px;
  z-index: 300;
  
}


.confirmComplete-header  {
  background-color:#4b0082;
  color:Gainsboro;
  text-align:left;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 0 0 1000px rgba(0,0,0,.3);
  
}



.confirmComplete-header h3{
  color:white;
  text-align:left;
  padding: 12px;
  margin:0px;
}


.confirmComplete-body {
  background-color:white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}


.confirmComplete-body h4 {
  float:left;
  padding: 12px 12px 24px 12px;
}




.winnerNamePoints {
  font-size: 3vh;
}



.winnerDetails {
  text-transform: capitalize;
  font-weight:900;

}


.resultsTableContainer {
  text-align:center;
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 5px;
}


.resultsTable {
  border-collapse: collapse;
  margin: 0 auto;
}


.resultsTable th {
  text-align:left;
  text-transform: capitalize;
  min-width: 140px;
  border: 1px solid #ccc;
  font-size: 3vh;
  padding: 2px;
}


.resultsTable td {
  font-weight:bold;
  background-color: #F5F5F5;
  border: 1px solid #ccc;
  font-size: 3vh;
  white-space:nowrap;
}




.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}


.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}


input:checked + .slider {
  background-color: #f1356d;
}

input:focus + .slider {
  box-shadow: 0 0 1px #f1356d;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}


/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}




.replayPlayersText {
  padding-left: 10px;
  font-weight: bold;
  position: relative;
  top: 5px;
  font-size: 2vh;
}




.results-button-container button {
  display: inline-block;
  background: #f1356d;
  color: #fff;
  border: none;
  padding: 1.5vh 8vh;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2.5vh;
  font-family: inherit;
  margin-top: 40px;
  margin-bottom: 30px;
  font-weight: bold;
}


.results-button-container button:focus {
  outline: none;
}



